.noModal{
   display: none; 
}

.modal{
    transition: all;
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.37);
    z-index: 10;
}

.modalWin{
    position: absolute;
    width: 40%;
    height: 30%;
    background-color: white;
    border-radius: 20px;
    left: 30%;
    right: 50%;
    top: 200px;
    max-width: 920px;
}

.modalTitle{
    position: relative;
    margin: 0 auto;
    text-align: center;
    color: rgb(92, 206, 235);
    border-bottom: 1px solid rgb(92, 206, 235);
    width: 300px;
    padding-bottom: 5px;
    padding-top: 15px;
}

.inputModalWrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
    margin-top: 9%;
}

.modalInput{
    outline: none;
    border: none;
    border-bottom: 1px solid black;
    border-left: 1px solid black;
    font-size: 16px;
    padding: 6px 10px 6px 10px;
    width: 50%;
}

.modalButton{
    padding: 10px;
    width: 30%;
    background-color: rgb(90, 241, 183);
    cursor: pointer;
    border-radius: 10px;
    text-align: center;
    font-size: 18px;
}

.modalButton:hover{
    background-color: rgb(32, 253, 169);
    transition: all;
    transition-duration: 150ms;
}

.modalClose{
    z-index: 20;
    position: absolute;
    left: 95%;
    top: 2%;
    width: 30px;
    color: rgb(221, 113, 113);
    cursor: pointer;
}

.modalClose:hover{
    color: rgb(255, 0, 0);
    zoom: 1.01;
    transition: all;
    transition-duration: 150ms;
}


@media (min-width: 1700px) {
    .modalWin{
        height: 280px;
    }
  }


@media (max-width: 1400px) {
    .modalWin{
        width: 70%;
        height: 35%;
        left: 15%;
        top: 200px;
    }
  }


@media (max-width: 1000px) {
    .modalWin{
        height: 35%;
    }
    
    .modalClose{
        left: 93%;
    }
  }


  @media (max-width: 750px) {
    .modalWin{
        width: 100%;
        height: 35%;
        left: 0;
        top: 200px;
    }


    .modalTitle{
        font-size: 24px;
        /* margin-top: 20px; */
    }

  }


  @media (max-width: 500px) {
    .modalWin{
        width: 100%;
        height: 35%;
        left: 0;
        top: 200px;
    }

    .modalTitle{
        font-size: 24px;
    }

    .modalClose{
        left: 90%;
    }

    .modalInput{
        width: 90%;
    }

    .modalButton{
        width: 90%;
    }

  }