.projectWrapper{
    padding-inline: 50px;
    background-color: rgba(92, 206, 235, 0.753);
    border-radius: 10px;
    font-size: 16px;
    cursor: pointer;
    width: 30%;
}

.projectWrapper:hover{
    background-color: rgba(50, 200, 238, 0.856);
    transition: all;
    transition-duration: 150ms;
}

@media screen and (max-width: 550px){
    .projectWrapper{
        min-width: 50%;
    }
}