.todoCartWrapper{
    /* border: 1px solid black; */
    box-shadow: 1px 1px 10px 1px rgba(89, 156, 219, 0.623) ;
    border-radius: 10px;
    min-width: 310px;
    padding: 5px;
}

.todoText{
    text-align: left;
}

.desc{
    margin: 0;
}

.title{
    margin: 4px;
    font-weight: bold;
}


@media (min-width:700px){
    .todoCartWrapper{
        /* border: 1px solid black; */
        box-shadow: 1px 1px 10px 1px rgba(89, 156, 219, 0.623) ;
        border-radius: 10px;
        min-width: 500px;
        padding: 5px;
    }
}