.wrapper{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 20px;
    width: 100%;
    margin-bottom: 50px;
}

.plusIcon{
    width: 30px;
}

.createProject{
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    padding: 2px;
    background-color: rgb(90, 241, 183);;
    max-width: 200px;
    border-radius: 10px;
}

.createProject:hover{
    background-color: rgb(32, 253, 169);
    cursor: pointer;
    transition: all;
    transition-duration: 150ms;
}