.noModal{
    display: none; 
 }
 
 .modal{
     transition: all;
     position: absolute;
     width: 100%;
     height: 100%;
     background-color: rgba(0, 0, 0, 0.37);
     z-index: 10;
 }
 
 .modalWin{
     position: absolute;
     width: 40%;
     height: 60%;
     background-color: white;
     border-radius: 20px;
     left: 30%;
     right: 50%;
     top: 200px;
     max-width: 920px;
 }
 
 .modalTitle{
     position: relative;
     margin: 0 auto;
     text-align: center;
     color: rgb(92, 206, 235);
     border-bottom: 1px solid rgb(92, 206, 235);
     width: 300px;
     padding-bottom: 5px;
     padding-top: 15px;
 }
 
 .inputModalWrapper{
     display: flex;
     flex-direction: column;
     align-items: center;
     justify-content: center;
     gap: 30px;
     margin-top: 9%;
 }
 
 .modalInput{
     outline: none;
     border: none;
     border-bottom: 1px solid black;
     border-left: 1px solid black;
     font-size: 16px;
     padding: 6px 10px 6px 10px;
     width: 50%;
 }
 
 .modalButton{
     padding: 10px;
     width: 30%;
     background-color: rgb(90, 241, 183);
     cursor: pointer;
     border-radius: 10px;
     text-align: center;
     font-size: 18px;
 }
 
 .modalButton:hover{
     background-color: rgb(32, 253, 169);
     transition: all;
     transition-duration: 150ms;
 }
 
 .modalClose{
     z-index: 20;
     position: absolute;
     left: 95%;
     top: 2%;
     width: 30px;
     color: rgb(221, 113, 113);
     cursor: pointer;
 }
 
 .modalClose:hover{
     color: rgb(255, 0, 0);
     zoom: 1.01;
     transition: all;
     transition-duration: 150ms;
 }
 
.radio{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 20px;
}

.radiowrap{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}