.statusWrap{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    padding-bottom: 5px;
    text-align: center;
    gap: 40px;
}

.orange{
    color: rgb(240, 186, 85);
    border-bottom: 1px solid;
    border-color: rgb(240, 186, 85);
    width: 300px;
}

.blue{
    color: rgb(79, 79, 241);
    border-bottom: 1px solid;
    border-color: rgb(79, 79, 241);
    width: 200px;
}

.green{
    color: rgb(108, 243, 108);
    border-bottom: 1px solid;
    border-color: rgb(108, 243, 108);
    width: 100px;
}

.createProject{
    margin-top: 40px;
}